/* Toggle Button */
.toggle{
  width: 165px;
  height: 100px;
  margin: 25px 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  box-shadow: 10px 10px 5px -5px rgba(0,0,0,0.3);
}


.toggle h5{
  font-size: 18px;
  font-weight: 600;
}
.buttons{
  display: flex;
  flex-direction: row;
  
}
.buttons button{
  /* background: none; */
  border: none;
  /* margin: 5px; */
  padding: 10px;
 
}

/* FeedBack */
.toggles{
  width: 200px;
  height: 100px;
  margin: 25px 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 7px;
  box-shadow: 10px 10px 5px -5px rgba(0,0,0,0.3);
}

.toggles button{
  border: none;
  /* margin: 5px; */
  padding: 7px;
  border-radius: 5px;
}

.toggles h5{
  font-size: 18px;
  font-weight: 600;
}
.toggles h7{
  font-weight: 600;
}



