}

.pagination a {
  color: black;
  float: left;
  padding: 5px 12px;
  text-decoration: none;
  transition: background-color .3s;
  border: 1px solid #ddd;
  margin: 0 4px;
  border-radius: 50%;
  background-color: white;
  box-shadow: 10px 10px 5px -5px rgba(0,0,0,0.3);
}

.pagination a.active {
  background-color: #4CAF50;
  color: white;
