.popup{
  width: 1000px;
  background: white;
  height: 420px;
  /* border: 0.1px solid; */
  overflow-y: auto;
  /* overflow-y: hidden; */
  padding: 40px;
  border-radius: 30px;
  position: relative;
  box-shadow: 10px 10px 5px -5px rgba(0,0,0,0.3);
}
.popup::-webkit-scrollbar{
  display: none;
}

.popup .close{
  box-sizing: content-box;
  
  padding: .25em .25em;
  color: #000;
  border-radius: .25rem;
  opacity: .5;
  position: absolute;
  top: 15px;
  right: 35px;
  border: none;
  height: auto;
  width: auto;
  background: transparent;
  
}

.popup-overlay{
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.popup .imge{
  margin: 15px 0px;
  width: 100%;
